<template>
  <div class="container-fluid p-0 secsion-search">
    <div class="container pt-4 pb-2">
      <h2 class="search-heading text-white">Tìm kiếm món ăn bạn yêu thích:</h2>
      <form @keypress.enter="showMsg">
        <div class="input-group mb-3">
          <input
            name="inp-search"
            type="text"
            class="form-control inp-search w-75"
            placeholder="Enter your favorite food ..."
          />
          <div class="input-group-append">
            <button @click="search" class="btn btn-search" type="button">
              Search
            </button>
          </div>
        </div>
      </form>

      <div class="result-search row">
        <div class="col-3 g-4" v-for="(item, index) in foodSearch" :key="index">
          <div class="card card-item">
            <img
              class="card-img-top h-50"
              :src="item.images[0].imageUrl"
              alt="ảnh sản phẩm"
            />
            <div class="card-body">
              <h4 class="card-title">{{ item.foodName }}</h4>
              <h6 class="card-text"><b>Giá:</b> {{ item.price }}vnđ</h6>
              <p class="card-text card-desc">
                <b>Mô tả:</b> {{ item.description }}
              </p>
              <p class="text-center mt-2 mb-0">
                <router-link
                  :to="{
                    name: 'Detail',
                    params: { idFood: item.foodId },
                  }"
                  class="btn-card view-detail d-inline-block w-100"
                  >Xem chi tiết</router-link
                >
              </p>
            </div>
          </div>
        </div>
      </div>
      <div class="scroll-top text-white">
        <a href="">123</a>
        <a href="#"
          ><i class="scroll-top-icon bi bi-arrow-up-square-fill"></i
        ></a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from "vuex";
export default {
  created() {},
  mounted() {},
  computed: {
    ...mapGetters(["foodSearch"]),
  },
  methods: {
    ...mapMutations(["search"]),
    ...mapActions(["search"]),
    showScollTop() {
      window.onscroll = function () {
        var btnScrolltop = document.querySelector(".scroll-top");
        if (
          document.documentElement.scrollTop >= 50 ||
          document.body.scrollTop >= 50
        ) {
          btnScrolltop.style.display = "block";
        } else {
          btnScrolltop.style.display = "none";
        }
      };
    },
    showMsg() {
      alert("Vui lòng nhấn nút Search để tìm kiếm !!!");
    },
  },
};
</script>

